.Modal__Wrapper {
  display: flex;
  height: 100%;
  flex-direction: column;
  gap: 1.5rem;
  padding: 1.25rem
}
  .Modal__Header {
  display: flex;
  flex-direction: column;
  gap: 0.75rem
}
  .Modal__Header__Close {
  cursor: pointer;
  align-self: flex-end;
  font-size: 1.5rem;
  line-height: 2rem;
  --tw-text-opacity: 1;
  color: rgb(34 34 34 / var(--tw-text-opacity))
}
  .Modal__Header__Close:hover {
  --tw-text-opacity: 1;
  color: rgb(220 48 24 / var(--tw-text-opacity))
}
  .Modal__Header__Title {
  font-size: 1.25rem;
  line-height: 1.75rem;
  font-weight: 800;
  --tw-text-opacity: 1;
  color: rgb(51 51 51 / var(--tw-text-opacity))
}
  @media (min-width: 768px) {
  .Modal__Header__Title {
        padding: 0 60px
    }
      }
  .Modal__Content {
  overflow: hidden
}
  @media (min-width: 768px) {
  .Modal__Content {
      padding: 0 60px 30px
  }
    }