.Sidebar {
  position: sticky;
  z-index: 20;
  display: flex;
  width: max-content;
  flex-direction: column;
  top: 54px
}

  .Sidebar__Wrapper {
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity))
}

  .Sidebar__Header {
  --tw-bg-opacity: 1;
  background-color: rgb(34 34 34 / var(--tw-bg-opacity));
  padding: 1.25rem;
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity))
}

  .Sidebar__Header__Toggle--open {
  margin-right: 1.25rem
}

  .Sidebar__Header__Title {
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: 700
}

  .Sidebar__Content {
  display: flex;
  flex-direction: column;
  gap: 0.5rem
}

  .Sidebar__Content__Wrapper {
  height: 100%;
  padding: 0.75rem
}

  .Sidebar__Add {
  display: flex;
  height: 2.25rem;
  align-items: center;
  gap: 0.75rem;
  border-radius: 0.375rem;
  --tw-bg-opacity: 1;
  background-color: rgb(235 235 235 / var(--tw-bg-opacity));
  padding: 0.5rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  --tw-text-opacity: 1;
  color: rgb(51 51 51 / var(--tw-text-opacity))
}

  .Sidebar__Add:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(245 245 245 / var(--tw-bg-opacity))
}

  .Sidebar__Add__Icon {
  width: 1.5rem
}

  .Sidebar__Add--withCustomIcon {
  height: max-content;
  flex-direction: column;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem
}

  .Sidebar__Add--expanded {
  width: 13rem
}

  .Sidebar__Add--collapsed {
  width: 2.25rem
}

  .Sidebar__Back {
  margin-bottom: 0.5rem;
  display: flex;
  width: 100%;
  align-items: center;
  gap: 0.75rem;
  border-radius: 0.375rem;
  --tw-bg-opacity: 1;
  background-color: rgb(235 235 235 / var(--tw-bg-opacity));
  padding: 0.5rem;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  --tw-text-opacity: 1;
  color: rgb(51 51 51 / var(--tw-text-opacity))
}

  .Sidebar__Back:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(245 245 245 / var(--tw-bg-opacity))
}

  .Sidebar__Back__Label {
  font-weight: 700
}