.EmptyList {
  margin-left: auto;
  margin-right: auto;
  margin-top: 4rem;
  margin-bottom: 4rem;
  display: flex;
  width: 66.666667%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 0.5rem;
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  padding-left: 5rem;
  padding-right: 5rem;
  padding-top: 2.5rem;
  padding-bottom: 3.5rem;
  text-align: center;
}@media (min-width: 1280px) {.EmptyList {
    width: 33.333333%;
  }
}.EmptyList {
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25);
}

  .EmptyList__Text {
  margin-top: 1rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
}

  @media (min-width: 1024px) {.EmptyList__Text {
    font-size: 1rem;
    line-height: 1.5rem;
  }
}