.BlockMultiColumns__Wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1rem
}

.BlockColumn {
  display: flex;
  flex-direction: column;
  border-radius: 0.375rem;
  border-left-width: 8px;
  --tw-border-opacity: 1;
  border-left-color: rgb(220 48 24 / var(--tw-border-opacity));
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25)
}

.BlockColumn__Content {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem
}

@media (min-width: 1280px) {

  .BlockColumn__Content {
    padding-top: 2rem;
    padding-bottom: 2rem;
    padding-left: 2.5rem;
    padding-right: 2.5rem
  }
}

.BlockColumn--closed {
  display: none
}

.ColumnIcon {
  display: flex;
  height: 0.5rem;
  overflow: hidden;
  border-radius: 9999px;
  border-width: 1px
}

.ColumnIcon--asCompactIcon {
  --tw-border-opacity: 1;
  border-color: rgb(34 34 34 / var(--tw-border-opacity));
  --tw-bg-opacity: 1;
  background-color: rgb(34 34 34 / var(--tw-bg-opacity))
}

.ColumnIcon--asLayoutIcon {
  --tw-border-opacity: 1;
  border-color: rgb(235 235 235 / var(--tw-border-opacity));
  --tw-bg-opacity: 1;
  background-color: rgb(235 235 235 / var(--tw-bg-opacity))
}

.ColumnIcon__Chip {
  margin-right: 1px
}

.ColumnIcon__Chip--asCompactIcon {
  --tw-bg-opacity: 1;
  background-color: rgb(235 235 235 / var(--tw-bg-opacity))
}

.ColumnIcon__Chip--asLayoutIcon {
  --tw-bg-opacity: 1;
  background-color: rgb(51 51 51 / var(--tw-bg-opacity))
}

.ColumnIcon__Chip--current {
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity))
}