.BlockSeparator__Config {
    margin-bottom: 1.5rem;
    display: flex;
    width: 100%;
    flex-direction: column;
    gap: 1rem
}
@media (min-width: 768px) {
    .BlockSeparator__Config {
        width: 50%
    }
}
  .BlockSeparator__Preview {
    border-radius: 0.375rem;
    border-width: 1px;
    border-style: dotted;
    --tw-border-opacity: 1;
    border-color: rgb(0 0 0 / var(--tw-border-opacity));
    padding: 1rem
}
  .BlockSeparator__Preview--element {
    height: 1px;
    --tw-bg-opacity: 1;
    background-color: rgb(0 0 0 / var(--tw-bg-opacity))
}