.BlockVideo__Frame {
    width: 100%;
}
@media (min-width: 1024px) {
    .BlockVideo__Frame {
        width: 33.333333%;
    }
}
  .BlockVideo__Content {
    margin-top: 1rem;
    border-radius: 0.375rem;
    border-left-width: 8px;
    --tw-border-opacity: 1;
    border-color: rgb(220 48 24 / var(--tw-border-opacity));
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity));
    padding: 1rem;
}
  @media (min-width: 768px) {
    .BlockVideo__Content {
        padding-left: 3.5rem;
        padding-right: 3.5rem;
        padding-top: 2rem;
        padding-bottom: 2rem;
    }
}
  .BlockVideo__Content {
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  }
  .BlockVideo__Content__Title {
    margin-bottom: 1rem;
    font-weight: 700;
    --tw-text-opacity: 1;
    color: rgb(51 51 51 / var(--tw-text-opacity));
}
  @media (min-width: 768px) {
    .BlockVideo__Content__Title {
        font-size: 1.25rem;
        line-height: 1.75rem;
    }
}
  @media (min-width: 1280px) {
    .BlockVideo__Content__Search {
        width: 66.666667%;
    }
}