.BlockProduct__Loader {
    position: absolute;
    top: 100%;
    width: 100%;
    overflow: hidden;
    border-radius: 0.375rem;
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity));
    padding-top: 1rem;
    padding-bottom: 1rem;
    text-align: center;
    font-size: 1.5rem;
    line-height: 2rem;
    --tw-text-opacity: 1;
    color: rgb(220 48 24 / var(--tw-text-opacity));
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25);
}
  .BlockProduct__Content {
    border-radius: 0.375rem;
    border-left-width: 8px;
    --tw-border-opacity: 1;
    border-color: rgb(220 48 24 / var(--tw-border-opacity));
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity));
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
}
  @media (min-width: 1024px) {
    .BlockProduct__Content {
        padding-left: 3.5rem;
        padding-right: 3.5rem;
        padding-top: 2rem;
        padding-bottom: 2rem;
    }
}
  .BlockProduct__Content {
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  }
  .BlockProduct__Content__Title {
    font-weight: 700;
    --tw-text-opacity: 1;
    color: rgb(51 51 51 / var(--tw-text-opacity));
}
  @media (min-width: 768px) {
    .BlockProduct__Content__Title {
        font-size: 1.25rem;
        line-height: 1.75rem;
    }
}
  .BlockProduct__Content__Search {
    position: relative;
    margin-top: 1rem;
}
  @media (min-width: 1280px) {
    .BlockProduct__Content__Search {
        width: 66.666667%;
    }
}
  .BlockProduct__Product {
    margin-bottom: 1rem;
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
    border-radius: 0.375rem;
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity));
    padding: 0.5rem;
}
  @media (min-width: 640px) {
    .BlockProduct__Product {
        height: 5rem;
    }
}
  @media (min-width: 768px) {
    .BlockProduct__Product {
        gap: 2rem;
    }
}
  .BlockProduct__Product__Loader {
    margin-bottom: 1rem;
    width: 100%;
    border-radius: 0.375rem;
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity));
    padding: 0.5rem;
    text-align: center;
}
  .BlockProduct__Product .Product__Image {
    display: none;
    height: 100%;
}
  @media (min-width: 640px) {
    .BlockProduct__Product .Product__Image {
        display: block;
    }
}
  .BlockProduct__Product .Product__Infos {
    display: flex;
    flex-direction: column;
}
  @media (min-width: 768px) {
    .BlockProduct__Product .Product__Infos {
        width: 20%;
        flex-direction: row;
        justify-content: space-between;
        gap: 1rem;
    }
}
  .BlockProduct__Product .Product__Infos__Title {
    text-align: left;
    font-weight: 600;
}
  .BlockProduct__Product .Product__Infos__Ref {
    text-align: left;
    font-size: 0.875rem;
    line-height: 1.25rem;
    --tw-text-opacity: 1;
    color: rgb(156 163 175 / var(--tw-text-opacity));
}
  @media (min-width: 768px) {
    .BlockProduct__Product .Product__Infos__Ref {
        font-size: 1rem;
        line-height: 1.5rem;
    }
}
  .BlockProduct__Product .Product__Actions {
    display: flex;
    gap: 2rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
}
  @media (min-width: 768px) {
    .BlockProduct__Product .Product__Actions {
        border-left-width: 1px;
        border-right-width: 1px;
        --tw-border-opacity: 1;
        border-color: rgb(156 163 175 / var(--tw-border-opacity));
        padding-left: 1rem;
        padding-right: 1rem;
    }
}
  .BlockProduct__Product .Product__Link {
    display: none;
    align-items: center;
    border-radius: 0.375rem;
    border-width: 2px;
    --tw-border-opacity: 1;
    border-color: rgb(220 48 24 / var(--tw-border-opacity));
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    text-align: center;
    font-weight: 600;
    letter-spacing: 0.05em;
    --tw-text-opacity: 1;
    color: rgb(220 48 24 / var(--tw-text-opacity));
}
  .BlockProduct__Product .Product__Link:hover {
    --tw-bg-opacity: 1;
    background-color: rgb(220 48 24 / var(--tw-bg-opacity));
    --tw-text-opacity: 1;
    color: rgb(255 255 255 / var(--tw-text-opacity));
}
  @media (min-width: 768px) {
    .BlockProduct__Product .Product__Link {
        padding-left: 1rem;
        padding-right: 1rem;
        padding-top: 0.25rem;
        padding-bottom: 0.25rem;
    }
}
  @media (min-width: 1024px) {
    .BlockProduct__Product .Product__Link {
        display: flex;
    }
}
  .BlockProduct__Product .Product__Link__Icon {
    display: none;
}
  @media (min-width: 768px) {
    .BlockProduct__Product .Product__Link__Icon {
        display: block;
    }
    .BlockProduct__Product .Product__Link__Label {
        margin-right: 0.5rem;
    }
}
  .BlockProduct__Product .Product__Delete {
    padding-right: 0.25rem;
}
  .BlockProduct__Product .Product__Delete:hover {
    --tw-text-opacity: 1;
    color: rgb(220 48 24 / var(--tw-text-opacity));
}
  @media (min-width: 768px) {
    .BlockProduct__Product .Product__Delete {
        align-self: flex-start;
    }
}
  .BlockProduct__Product .Product__Delete > svg {
    height: 0.75rem;
    width: 0.75rem;
}

.ProductList {
    position: absolute;
    top: 100%;
    z-index: 30;
    width: 100%;
    overflow: scroll;
    border-radius: 0.375rem;
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  max-height: 200px;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25);
}

.ProductList__Item {
    display: flex;
    cursor: pointer;
    flex-direction: column;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
}

.ProductList__Item:hover {
    --tw-bg-opacity: 1;
    background-color: rgb(229 231 235 / var(--tw-bg-opacity));
}

.ProductList__Item__Ref {
    font-size: 0.875rem;
    line-height: 1.25rem;
    --tw-text-opacity: 1;
    color: rgb(156 163 175 / var(--tw-text-opacity));
}

.ProductList__NoResults {
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    text-align: center;
}

.emphasize {
    font-weight: 700;
    --tw-text-opacity: 1;
    color: rgb(220 48 24 / var(--tw-text-opacity));
}