.Preview__Loader {
    font-size: 2.25rem;
    line-height: 2.5rem;
    --tw-text-opacity: 1;
    color: rgb(115 206 111 / var(--tw-text-opacity))
}
  .Preview__Close {
    --tw-bg-opacity: 1;
    background-color: rgb(220 48 24 / var(--tw-bg-opacity));
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    --tw-text-opacity: 1;
    color: rgb(255 255 255 / var(--tw-text-opacity))
}
  .Preview__Modal {
    display: flex;
    flex-direction: column;
    padding: 1rem
}
  .Preview__Modal__Close {
    align-self: flex-end
}
  .Preview__Modal__Close__Icon {
    font-size: 1.25rem;
    line-height: 1.75rem;
    --tw-text-opacity: 1;
    color: rgb(34 34 34 / var(--tw-text-opacity))
}
  .Preview__Modal__Close__Icon:hover {
    --tw-text-opacity: 1;
    color: rgb(220 48 24 / var(--tw-text-opacity))
}
  .Preview__Modal__Title {
    margin-top: 0.75rem;
    margin-bottom: 1.5rem;
    text-align: center;
    font-weight: 800;
    --tw-text-opacity: 1;
    color: rgb(51 51 51 / var(--tw-text-opacity))
}
  @media (min-width: 768px) {
    .Preview__Modal__Title {
        text-align: left;
        font-size: 1.25rem;
        line-height: 1.75rem
    }
}