.Search__Loader {
    position: absolute;
    top: 100%;
    width: 100%;
    overflow: hidden;
    border-radius: 0.375rem;
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity));
    padding-top: 1rem;
    padding-bottom: 1rem;
    text-align: center;
    font-size: 1.5rem;
    line-height: 2rem;
    --tw-text-opacity: 1;
    color: rgb(220 48 24 / var(--tw-text-opacity));
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25);
}
  .Search__Content {
    border-radius: 0.375rem;
    border-left-width: 8px;
    --tw-border-opacity: 1;
    border-color: rgb(220 48 24 / var(--tw-border-opacity));
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity));
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
}
  @media (min-width: 1024px) {
    .Search__Content {
        padding-left: 3.5rem;
        padding-right: 3.5rem;
        padding-top: 2rem;
        padding-bottom: 2rem;
    }
}
  .Search__Content {
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  }
  .Search__Content__Title {
    font-weight: 700;
    --tw-text-opacity: 1;
    color: rgb(51 51 51 / var(--tw-text-opacity));
}
  @media (min-width: 768px) {
    .Search__Content__Title {
        font-size: 1.25rem;
        line-height: 1.75rem;
    }
}
  .Search__Content__Search {
    position: relative;
    margin-top: 1rem;
}
  @media (min-width: 1280px) {
    .Search__Content__Search {
        width: 66.666667%;
    }
}

.SearchResult {
    position: absolute;
    top: 100%;
    z-index: 30;
    width: 100%;
    overflow: scroll;
    border-radius: 0.375rem;
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  max-height: 200px;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25);
}

.SearchResult__Item {
    display: flex;
    cursor: pointer;
    flex-direction: column;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
}

.SearchResult__Item:hover {
    --tw-bg-opacity: 1;
    background-color: rgb(229 231 235 / var(--tw-bg-opacity));
}

.SearchResult__Item__Ref {
    font-size: 0.875rem;
    line-height: 1.25rem;
    --tw-text-opacity: 1;
    color: rgb(156 163 175 / var(--tw-text-opacity));
}

.SearchResult__NoResults {
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    text-align: center;
}