.AddBlocks {

    display: flex;

    flex-direction: column;

    align-items: center;

    justify-content: center;

    border-radius: 0.375rem;

    border-width: 1px;

    border-style: dotted;

    --tw-border-opacity: 1;

    border-color: rgb(120 120 120 / var(--tw-border-opacity));

    padding: 1rem
}

  .AddBlocks__Icon {

    border-radius: 9999px;

    --tw-bg-opacity: 1;

    background-color: rgb(245 245 245 / var(--tw-bg-opacity));

    padding: 0.5rem
}

  .AddBlocks__Info {

    margin-top: 0.75rem;

    margin-bottom: 0.75rem;

    text-align: center
}

  .AddBlocks__Button {

    width: max-content;

    border-radius: 0.375rem;

    border-width: 2px;

    --tw-border-opacity: 1;

    border-color: rgb(220 48 24 / var(--tw-border-opacity));

    padding-left: 0.5rem;

    padding-right: 0.5rem;

    padding-top: 0.25rem;

    padding-bottom: 0.25rem;

    font-size: 0.75rem;

    line-height: 1rem;

    font-weight: 600;

    text-transform: uppercase;

    letter-spacing: 0.05em;

    --tw-text-opacity: 1;

    color: rgb(220 48 24 / var(--tw-text-opacity))
}

  .AddBlocks__Button:hover {

    --tw-bg-opacity: 1;

    background-color: rgb(220 48 24 / var(--tw-bg-opacity));

    --tw-text-opacity: 1;

    color: rgb(255 255 255 / var(--tw-text-opacity))
}

  .AddBlocks__Modal {

    display: flex;

    flex-direction: column;

    padding: 1rem
}

  .AddBlocks__Modal__Close {

    align-self: flex-end
}

  .AddBlocks__Modal__Close__Icon {

    font-size: 1.25rem;

    line-height: 1.75rem;

    --tw-text-opacity: 1;

    color: rgb(34 34 34 / var(--tw-text-opacity))
}

  .AddBlocks__Modal__Close__Icon:hover {

    --tw-text-opacity: 1;

    color: rgb(220 48 24 / var(--tw-text-opacity))
}

  @media (min-width: 1024px) {

    .AddBlocks__Modal__Content {

        padding-left: 3rem;

        padding-right: 3rem;

        padding-bottom: 3rem
    }
}

  .AddBlocks__Modal__Title {

    margin-top: 0.75rem;

    margin-bottom: 1.5rem;

    text-align: center;

    font-weight: 800;

    --tw-text-opacity: 1;

    color: rgb(51 51 51 / var(--tw-text-opacity))
}

  @media (min-width: 768px) {

    .AddBlocks__Modal__Title {

        text-align: left;

        font-size: 1.25rem;

        line-height: 1.75rem
    }
}

  .AddBlocks__Modal__BlocksList {

    display: flex;

    flex-wrap: wrap;

    justify-content: center;

    gap: 1rem
}

  @media (min-width: 1024px) {

    .AddBlocks__Modal__BlocksList {

        justify-content: flex-start
    }
}

  .AddBlocks__Modal__BlocksList__Wrapper {

    display: flex;

    flex-wrap: wrap
}

  .AddBlocks__Modal__LayoutBlocksList {

    display: flex;

    width: 100%;

    flex-wrap: wrap;

    gap: 0.5rem
}

  .AddBlocks__Modal__Add {

    display: flex;

    height: 6rem;

    width: 6rem;

    flex-direction: column;

    align-items: center;

    justify-content: center;

    gap: 0.5rem;

    border-radius: 0.375rem;

    --tw-bg-opacity: 1;

    background-color: rgb(235 235 235 / var(--tw-bg-opacity));

    font-size: 0.875rem;

    line-height: 1.25rem;

    --tw-text-opacity: 1;

    color: rgb(51 51 51 / var(--tw-text-opacity))
}

  .AddBlocks__Modal__Add:hover {

    --tw-bg-opacity: 1;

    background-color: rgb(245 245 245 / var(--tw-bg-opacity))
}

  @media (min-width: 768px) {

    .AddBlocks__Modal__Add {

        height: 7rem;

        width: 7rem
    }
}