.Block {
  background-image: linear-gradient(to bottom, var(--tw-gradient-stops));
  --tw-gradient-from: #EBEBEB;
  --tw-gradient-to: rgb(235 235 235 / 0);
  --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to);
  --tw-gradient-to: #F5F5F5;
  padding: 1rem;
}

@media (min-width: 640px) {

  .Block {
    padding: 2rem;
  }
}

@media (min-width: 768px) {

  .Block {
    padding-left: 8rem;
    padding-right: 8rem;
  }
}

@media (min-width: 1024px) {

  .Block {
    padding-left: 6rem;
    padding-right: 6rem;
  }
}

@media (min-width: 1280px) {

  .Block {
    padding-left: 11rem;
    padding-right: 11rem;
  }
}

@media (min-width: 1536px) {

  .Block {
    padding-left: 18rem;
    padding-right: 18rem;
  }
}

  .Block__Header {
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-between;
}

  .Block__Header__Infos {
  display: flex;
  align-items: center;
  gap: 1rem;
}

  .Block__Header__Infos__Title {
  font-size: 1.25rem;
  line-height: 1.75rem;
  font-weight: 800;
  --tw-text-opacity: 1;
  color: rgb(34 34 34 / var(--tw-text-opacity));
}

.NestedBlock {
  margin-bottom: 0.75rem;
  border-radius: 0.375rem;
  border-left-width: 8px;
  --tw-border-opacity: 1;
  border-left-color: rgb(255 143 128 / var(--tw-border-opacity));
  --tw-bg-opacity: 1;
  background-color: rgb(245 245 245 / var(--tw-bg-opacity));
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
  padding-left: 1rem;
  padding-right: 1rem;
}

@media (min-width: 768px) {

  .NestedBlock {
    padding-left: 2.75rem;
    padding-right: 2.75rem;
  }
}

.NestedBlock {
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
}

.NestedBlock__Header {
  margin-bottom: 0.75rem;
  display: flex;
  justify-content: space-between;
}

.NestedBlock__Header__Infos {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.NestedBlock__Header__Infos__Title {
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: 700;
  --tw-text-opacity: 1;
  color: rgb(51 51 51 / var(--tw-text-opacity));
}