.GroupLink__Wrapper {
    width: 100%
}
@media (min-width: 768px) {
    .GroupLink__Wrapper {
        width: 50%
    }
}
  .GroupLink__Input__Wrapper {
    display: flex;
    align-items: flex-end;
    gap: 0.5rem
}
  .GroupLink__GroupList__Loader {
    position: absolute;
    top: 100%;
    width: 100%;
    overflow: hidden;
    border-radius: 0.375rem;
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity));
    padding-top: 1rem;
    padding-bottom: 1rem;
    text-align: center;
    font-size: 1.5rem;
    line-height: 2rem;
    --tw-text-opacity: 1;
    color: rgb(220 48 24 / var(--tw-text-opacity))
}
  .GroupLink__LinkButton {
    height: 2.5rem;
    border-radius: 0.375rem;
    --tw-bg-opacity: 1;
    background-color: rgb(220 48 24 / var(--tw-bg-opacity));
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    font-size: 0.875rem;
    line-height: 1.25rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    --tw-text-opacity: 1;
    color: rgb(255 255 255 / var(--tw-text-opacity))
}
  .GroupLink__LinkButton:hover:enabled {
    --tw-bg-opacity: 1;
    background-color: rgb(250 83 60 / var(--tw-bg-opacity))
}
  .GroupLink__LinkButton:disabled {
    --tw-bg-opacity: 1;
    background-color: rgb(167 167 167 / var(--tw-bg-opacity));
    --tw-text-opacity: 1;
    color: rgb(255 255 255 / var(--tw-text-opacity))
}
  @media (min-width: 768px) {
    .GroupLink__LinkButton {
        padding-left: 1rem;
        padding-right: 1rem;
        padding-top: 0.25rem;
        padding-bottom: 0.25rem
    }
}