.BlockAccordion {
  display: flex;
  flex-direction: column;
  border-radius: 0.375rem;
  border-left-width: 8px;
  --tw-border-opacity: 1;
  border-left-color: rgb(220 48 24 / var(--tw-border-opacity));
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25)
}

  .BlockAccordion__Content {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem
}

  @media (min-width: 1280px) {

  .BlockAccordion__Content {
    padding-top: 2rem;
    padding-bottom: 2rem;
    padding-left: 2.5rem;
    padding-right: 2.5rem
  }
}

  .BlockAccordion--closed {
  display: none
}