.Toolbar {

    position: sticky;

    bottom: 0px;

    z-index: 20;

    display: flex;

    height: 5rem;

    width: 100%;

    align-items: center;

    justify-content: flex-end;

    gap: 0.5rem;

    --tw-bg-opacity: 1;

    background-color: rgb(68 68 68 / var(--tw-bg-opacity));

    padding-left: 1rem;

    padding-right: 1rem;

    padding-top: 1.25rem;

    padding-bottom: 1.25rem;

    --tw-text-opacity: 1;

    color: rgb(255 255 255 / var(--tw-text-opacity))
}

@media (min-width: 768px) {

    .Toolbar {

        padding-left: 3rem;

        padding-right: 3rem
    }
}

@media (min-width: 1280px) {

    .Toolbar {

        padding-left: 11rem;

        padding-right: 11rem
    }
}

@media (min-width: 1536px) {

    .Toolbar {

        padding-left: 15rem;

        padding-right: 15rem
    }
}

  .Toolbar__View {

    height: 100%;

    border-radius: 0.375rem;

    border-width: 1px;

    --tw-border-opacity: 1;

    border-color: rgb(255 255 255 / var(--tw-border-opacity));

    padding-left: 0.75rem;

    padding-right: 0.75rem;

    padding-top: 0.25rem;

    padding-bottom: 0.25rem;

    font-size: 0.875rem;

    line-height: 1.25rem;

    font-weight: 600;

    text-transform: uppercase;

    letter-spacing: 0.05em
}

  .Toolbar__View:hover {

    --tw-text-opacity: 1;

    color: rgb(0 0 0 / var(--tw-text-opacity))
}

  .Toolbar__View:hover:enabled {

    --tw-bg-opacity: 1;

    background-color: rgb(255 255 255 / var(--tw-bg-opacity))
}

  .Toolbar__View:disabled {

    --tw-text-opacity: 1;

    color: rgb(167 167 167 / var(--tw-text-opacity))
}

  .Toolbar__View__Icon {

    margin-right: 0.75rem;

    font-size: 1rem;

    line-height: 1.5rem
}

  .Toolbar__Save {

    height: 100%;

    border-radius: 0.375rem;

    --tw-bg-opacity: 1;

    background-color: rgb(220 48 24 / var(--tw-bg-opacity));

    padding-left: 0.5rem;

    padding-right: 0.5rem;

    font-size: 0.875rem;

    line-height: 1.25rem;

    font-weight: 600;

    text-transform: uppercase;

    letter-spacing: 0.05em;

    --tw-text-opacity: 1;

    color: rgb(255 255 255 / var(--tw-text-opacity))
}

  .Toolbar__Save:hover:enabled {

    --tw-bg-opacity: 1;

    background-color: rgb(250 83 60 / var(--tw-bg-opacity))
}

  .Toolbar__Save:disabled {

    --tw-text-opacity: 1;

    color: rgb(167 167 167 / var(--tw-text-opacity))
}

  @media (min-width: 768px) {

    .Toolbar__Save {

        padding-left: 1rem;

        padding-right: 1rem;

        padding-top: 0.25rem;

        padding-bottom: 0.25rem
    }
}

  .Toolbar__Save__Icon {

    margin-right: 0.75rem;

    font-size: 1rem;

    line-height: 1.5rem
}