.Input__Wrapper__Header {
    display: flex;
    justify-content: space-between;
}
    .Input__Wrapper__Error {
    margin-top: 0.25rem;
    font-size: 0.875rem;
    line-height: 1.25rem;
    --tw-text-opacity: 1;
    color: rgb(209 0 0 / var(--tw-text-opacity));
}
  .Input__Text,  .Input__TextArea,  .Input__Select,  .Input__Number {
    position: relative;
    width: 100%;
    border-radius: 0.375rem;
    border-width: 1px;
    --tw-border-opacity: 1;
    border-color: rgb(167 167 167 / var(--tw-border-opacity));
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 1rem;
    padding-right: 1rem;
    outline: 2px solid transparent;
    outline-offset: 2px;
}
  .Input__Text__Wrapper,  .Input__TextArea__Wrapper,  .Input__Select__Wrapper,  .Input__Number__Wrapper {
    position: relative;
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    align-items: stretch;
}
  .Input__Text--withIcon,  .Input__TextArea--withIcon,  .Input__Select--withIcon,  .Input__Number--withIcon {
    padding-left: 2.5rem;
}
  .Input__Text--withIcons,  .Input__TextArea--withIcons,  .Input__Select--withIcons,  .Input__Number--withIcons {
    padding-left: 2.5rem;
    padding-right: 2.5rem;
}
  .Input__Text, .Input__Select, .Input__Number {
    height: 40px;
  }
  .Input__TextArea {
    height: 290px;
  }
  .Input__Icon {
    position: absolute;
    display: flex;
    height: 100%;
    width: 2rem;
    align-items: center;
    justify-content: center;
    border-radius: 0.25rem;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    text-align: center;
    font-size: 1rem;
    line-height: 1.5rem;
    font-weight: 400;
    line-height: 1.375;
}
  .Input__Icon--left {
    left: 0px;
}
  .Input__Icon--right {
    right: 0px;
}
